<template>
  <div ref='wrapper'>
    <!-- div -->
    <!--  -->
    <!--  -->
    <slot></slot>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  props: {
    probeType: { //  scroll 
      type: Number,
      default: 1
    },
    click: { //  event  _constructed true
      type: Boolean,
      default: true
    },
    data: { //  watch refresh
      type: Array,
      default: null
    },
    listenScroll: { //  scroll 
      type: Boolean,
      default: false
    },
    pullup: {
      type: Boolean,
      default: false
    },
    beforeScroll: {
      type: Boolean,
      default: false
    },
    refreshDelay: {
      type: Number,
      default: 20
    }
  },
  // datarefresh
  watch: {
    data () {
      setTimeout(() => {
        this.refresh()
      }, this.refreshDelay)
    }
  },
  mounted () {
    setTimeout(() => {
      this._initScroll()
    }, this.refreshDelay) // nextTick
  },
  methods: {
    _initScroll () {
      if (!this.$refs.wrapper) {
        return
      }
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        click: this.click
      })
      // scroll
      if (this.listenScroll) {
        let that = this //  on  Vue  this.scrollVue
        this.scroll.on('scroll', (pos) => { //  scroll ,  scroll
          that.$emit('scroll', pos) // 
        })
      }
      if (this.pullup) {
        //  scrollEnd 
        this.scroll.on('scrollEnd', () => {
          if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
            this.$emit('scrollToEnd')
          }
        })
      }
      if (this.beforeScroll) {
        this.scroll.on('beforeScrollStart', () => {
          this.$emit('beforeScroll')
        })
      }
    },
    enable () { //  better-scroll,  
      this.scroll && this.scroll.enable()
    },
    disable () { //  better-scrollDOM  touchstarttouchmovetouchend
      this.scroll && this.scroll.disable()
    },
    refresh () {
      this.scroll && this.scroll.refresh()
    },
    // 
    scrollTo () { // scrollTo(x, y, time, easing)
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    },
    scrollToElement () { // scrollToElement(el, time, offsetX, offsetY, easing)
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    }
  }
}
</script>
