<template>
  <Scroll class='listview' :data='data' :listenScroll=true :probeType=3 @scroll='scroll' ref='listview'>
    <ul>
      <li class='list-group' v-for='(group, index) in data' :key='index' ref='listGroup'>
        <h2 class="list-group-title">{{group.title}}</h2>
        <ul>
          <li class="list-group-item" @click='selectItem(item)' v-for='(item, index) in group.items' :key='index'>
            <img class='avatar' v-lazy="item.avatar">
            <span class="name">{{item.name}}</span>
          </li>
        </ul>
      </li>
    </ul>
    <!--  -->
    <!-- stop->; prevent->; touchstart, touchmove, touchend  HTML5  -->
    <div class="list-shortcut" @touchstart='onShortcutTouchStart' @touchmove.stop.prevent='onShortcutTouchMove'>
      <ul>
        <!-- data-index li -->
        <li class="item" :class="{'current': currentIndex === index}" :data-index='index' v-for="(item, index) in shortcutList" :key='index'>
          {{item}}
        </li>
      </ul>
    </div>
    <div class="list-fixed" v-show='fixedTitle' ref='fixed'>
      <div class="fixed-title">{{fixedTitle}}</div>
    </div>
    <div class="loading-container" v-if='!data.length'>
      <Loading></Loading>
    </div>
  </Scroll>
</template>

<script>
import Scroll from 'base/scroll/scroll'
import Loading from 'base/loading/loading'
import { getData } from 'common/js/dom'

const ANCHOR_HEIGHT = 18
const FIXED_HEIGHT = 30

export default {
  props: {
    data: { //  title, items 
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      scrollY: 1, // BSy
      currentIndex: 0,
      diff: 0
    }
  },
  computed: {
    shortcutList () {
      // Array.prototype.map(func) -> func
      return this.data.map((group) => {
        return group.title.substr(0, 1)
      })
    },
    fixedTitle () {
      if (this.scrollY < 0) {
        return ''
      }
      return this.data[this.currentIndex] ? this.data[this.currentIndex].title : ''
    }
  },
  watch: {
    data () { // propsdata, 
      setTimeout(() => {
        this._calculateHeight()
      }, 20) // 
    },
    // 
    scrollY (newY) { // clear
      let listHeight = this.listHeight
      let len = listHeight.length
      let h1, h2
      for (let i = 0; i < len; i++) {
        h1 = listHeight[i]
        h2 = listHeight[i + 1] || listHeight[i]
        if (newY >= h1 && newY < h2) {
          this.currentIndex = i
          this.diff = h2 - newY - FIXED_HEIGHT
        } else if (newY >= listHeight[len - 1]) {
          this.currentIndex = len - 1
        } else if (newY < 0) {
          this.currentIndex = 0
        }
      }
    },
    // fixedTitle (x) { console.log(x) } // 
    diff (val) {
      let fixedTop = (val < 0) ? val : 0
      if (this.fixedTop === fixedTop) { // DOM
        return
      }
      this.fixedTop = fixedTop // 0
      this.$refs.fixed.style.transform = `translate3d(0, ${fixedTop}px, 0)`
    }
  },
  // createdvue
  created () {
    this.touch = {
      y1: 0,
      y2: 0,
      anchorIndex: 0,
      fixedTop: 0
    } //  shortcutList  touch
    this.listScroll = true
    this.listHeight = []
  },
  methods: {
    // 
    onShortcutTouchStart (e) {
      // console.log(e) //  e  TouchEvent
      let anchorIndex = parseInt(getData(e.target, 'index')) //  e.target  dataset index
      let firstTouch = e.touches[0] // e  touches 0 touch 
      this.touch.y1 = firstTouch.pageY // pageY = clientY + 
      this.touch.anchorIndex = anchorIndex
      this._scrollTo(anchorIndex)
      // this.currentIndex = anchorIndex
    },
    // 
    onShortcutTouchMove (e) {
      let firstTouch = e.touches[0]
      this.touch.y2 = firstTouch.pageY
      let delta = parseInt((this.touch.y2 - this.touch.y1) / ANCHOR_HEIGHT) // 
      let anchorIndex = this.touch.anchorIndex + delta
      this._scrollTo(anchorIndex)
    },
    scroll (pos) { //  Scroll  pos = { x: *, y: * }
      this.scrollY = -pos.y // BSy, 
    },
    selectItem (item) { // 
      this.$emit('select', item)
    },
    // refresh
    refresh () {
      this.$refs.listview.refresh()
    },
    _scrollTo (index) { // shortcut  BScroll
      // 2
      // scrollToElement(el, time, offsetX, offsetY, easing)
      //  listGroup  v-for listGroup  Array 
      if (!index && index !== 0) {
        return // index
      }
      if (index < 0) {
        index = 0
      } else if (index > this.listHeight.length - 2) {
        index = this.listHeight.length - 2
      }
      this.currentIndex = index // 
      this.$refs.listview.scrollToElement(this.$refs.listGroup[index], 0)
    },
    _calculateHeight () { // 
      this.listHeight = [] // 
      const list = this.$refs.listGroup // Array
      let height = 0
      this.listHeight.push(height)
      for (let i = 0; i < list.length; i++) {
        let item = list[i]
        height += item.clientHeight
        this.listHeight.push(height)
      }
    }
  },
  components: {
    Scroll, Loading
  }
}
</script>

